<template>
  <div class="canvas-container">
    <canvas ref="canvas"></canvas>
  </div>
</template>

<script>
export default {
  name: "CanvasComponent",

  props: {
    imageSrc: Object,
    scalePercent: Number
  },

  data() {
    return {
      originalPixels: null,
      originalWidth: 0,
      originalHeight: 0,
      scale: 1
    };
  },

  watch: {
    imageSrc(newVal) {
      if (!newVal) return;

      if (newVal.type === "image") {
        this.loadStandardImage(newVal.src);
      } else if (newVal.type === "gb7") {
        this.loadGB7(newVal);
      }
    },

    scalePercent(newVal) {
      this.scale = newVal / 100;
      this.redraw();
    }
  },

  methods: {
    loadStandardImage(src) {
      const img = new Image();
      img.onload = () => {
        this.originalWidth = img.width;
        this.originalHeight = img.height;

        const off = document.createElement("canvas");
        off.width = img.width;
        off.height = img.height;

        const ctx = off.getContext("2d");
        ctx.drawImage(img, 0, 0);

        this.originalPixels = ctx.getImageData(
          0, 0, img.width, img.height
        ).data;

        this.fitToScreen();
      };
      img.src = src;
    },

    loadGB7({ width, height, pixels }) {
      this.originalWidth = width;
      this.originalHeight = height;
      this.originalPixels = pixels;
      this.fitToScreen();
    },

    fitToScreen() {
      const container = this.$el;
      const maxW = container.clientWidth - 100;
      const maxH = container.clientHeight - 100;

      const scale = Math.min(
        maxW / this.originalWidth,
        maxH / this.originalHeight,
        1
      );

      this.scale = scale;
      this.$emit("scale-changed", Math.round(scale * 100));
      this.redraw();
    },

    redraw() {
      if (!this.originalPixels) return;

      const dstW = Math.round(this.originalWidth * this.scale);
      const dstH = Math.round(this.originalHeight * this.scale);

      const canvas = this.$refs.canvas;
      canvas.width = dstW;
      canvas.height = dstH;

      const pixels = this.bilinear(
        this.originalPixels,
        this.originalWidth,
        this.originalHeight,
        dstW,
        dstH
      );

      const ctx = canvas.getContext("2d");
      ctx.putImageData(new ImageData(pixels, dstW, dstH), 0, 0);
    },

    bilinear(src, sw, sh, dw, dh) {
      const dst = new Uint8ClampedArray(dw * dh * 4);

      for (let y = 0; y < dh; y++) {
        for (let x = 0; x < dw; x++) {
          const gx = (x + 0.5) * sw / dw - 0.5;
          const gy = (y + 0.5) * sh / dh - 0.5;

          const x0 = Math.floor(gx);
          const y0 = Math.floor(gy);
          const x1 = Math.min(x0 + 1, sw - 1);
          const y1 = Math.min(y0 + 1, sh - 1);

          const dx = gx - x0;
          const dy = gy - y0;

          for (let c = 0; c < 4; c++) {
            const i00 = (y0 * sw + x0) * 4 + c;
            const i10 = (y0 * sw + x1) * 4 + c;
            const i01 = (y1 * sw + x0) * 4 + c;
            const i11 = (y1 * sw + x1) * 4 + c;

            dst[(y * dw + x) * 4 + c] =
              src[i00] * (1 - dx) * (1 - dy) +
              src[i10] * dx * (1 - dy) +
              src[i01] * (1 - dx) * dy +
              src[i11] * dx * dy;
          }
        }
      }
      return dst;
    },

    nearest(srcData, srcW, srcH, dstW, dstH) {
      const dst = new Uint8ClampedArray(dstW * dstH * 4);

      for (let y = 0; y < dstH; y++) {
        for (let x = 0; x < dstW; x++) {
          const sx = Math.floor(x * srcW / dstW);
          const sy = Math.floor(y * srcH / dstH);
          const sIdx = (sy * srcW + sx) * 4;
          const dIdx = (y * dstW + x) * 4;
          dst.set(srcData.slice(sIdx, sIdx + 4), dIdx);
        }
      }
      return dst;
    },

    resizeImage(newW, newH, method) {
      const algo =
        method === "nearest"
          ? this.nearest
          : this.bilinear;

      const pixels = algo(
        this.originalPixels,
        this.originalWidth,
        this.originalHeight,
        newW,
        newH
      );

      this.originalPixels = pixels;
      this.originalWidth = newW;
      this.originalHeight = newH;

      this.scale = 1;
      this.redraw();
    }

  }
};
</script>

<style scoped>
.canvas-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 64px - 32px);
  background: #fff;
  margin-top: 64px;
  padding: 50px;
}
canvas {
  display: block;
}
</style>