<template>
  <v-app>
    <MenuComponent
      :scale-percent="scalePercent"
      @image-loaded="onImageLoaded"
      @scale-changed="onScaleChanged"
      @open-resize="showResize = true"
    />

    <ResizeComponent
      v-model="showResize"
      :width="imageSrc?.width"
      :height="imageSrc?.height"
      @confirm="applyResize"
      @open-resize="() => { console.log('OPEN'); showResize = true }"
    />

    <CanvasComponent
      ref="canvas"
      :imageSrc="imageSrc"
      :scalePercent="scalePercent"
    />

    <v-footer
      app
      height="32"
      color="grey-darken-3"
      class="white--text px-4 d-flex align-center justify-center"
    >
      <div v-if="statusText">{{ statusText }}</div>
      <div v-else>Загрузите изображение</div>
    </v-footer>
  </v-app>
</template>

<script>
import MenuComponent from "./components/MenuComponent.vue";
import CanvasComponent from "./components/CanvasComponent.vue";
import ResizeComponent from "./components/ResizeComponent.vue";

export default {
  name: "App",
  components: { MenuComponent, CanvasComponent, ResizeComponent },

  data() {
    return {
      imageSrc: null,
      scalePercent: 100,
      statusText: "",
      showResize: false,
    };
  },

  methods: {
    onImageLoaded(data) {
      this.imageSrc = data;
      this.scalePercent = 100;

      const { width, height, depth } = data;
      this.statusText = `Размер: ${width}×${height} px | Глубина цвета: ${depth} бит`;
    },

    onScaleChanged(value) {
      this.scalePercent = value;
    },

    applyResize({ width, height, interpolation }) {
      this.$refs.canvas.resizeImage(width, height, interpolation);
    }
  }
};
</script>