<template>
  <v-dialog v-model="open" max-width="520">
    <v-card>
      <v-card-title>Изменение размера изображения</v-card-title>

      <v-card-text>
        <div class="mb-3">
          <div>До: {{ mpBefore }} Мп</div>
          <div>После: {{ mpAfter }} Мп</div>
        </div>

        <v-select
          :items="['Проценты', 'Пиксели']"
          v-model="mode"
          label="Режим изменения"
          density="compact"
        />

        <v-text-field
          v-model.number="widthValue"
          label="Ширина"
          type="number"
          density="compact"
        />

        <v-text-field
          v-model.number="heightValue"
          label="Высота"
          type="number"
          density="compact"
        />

        <v-checkbox
          v-model="keepRatio"
          label="Сохранять пропорции"
          color="black"
          style="color: black;"
          hide-details
        />

        <v-select
          :items="interpolations"
          item-title="label"
          item-value="value"
          v-model="interpolation"
          label="Интерполяция"
          density="compact"
        >
          <template #append-inner>
            <v-tooltip activator="parent" location="end">
              {{ interpolationHint }}
            </v-tooltip>
          </template>
        </v-select>
      </v-card-text>

      <v-card-actions>
        <v-spacer />
        <v-btn variant="text" @click="close">Отмена</v-btn>
        <v-btn color="primary" @click="confirm">Применить</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "ResizeComponent",

  props: {
    modelValue: Boolean,
    width: Number,
    height: Number
  },

  emits: ["update:modelValue", "confirm"],

  data() {
    return {
      mode: "Проценты",
      widthValue: 100,
      heightValue: 100,
      keepRatio: true,
      interpolation: "bilinear",
      interpolations: [
        {
          label: "Билинейная",
          value: "bilinear",
          hint: "Плавное масштабирование, лучшее качество"
        },
        {
          label: "Ближайший сосед",
          value: "nearest",
          hint: "Быстро, но возможна пикселизация"
        }
      ]
    };
  },

  computed: {
    open: {
      get() {
        return this.modelValue;
      },
      set(v) {
        this.$emit("update:modelValue", v);
      }
    },

    mpBefore() {
      return (this.width * this.height / 1e6).toFixed(2);
    },

    mpAfter() {
      const w = this.newWidth;
      const h = this.newHeight;
      return ((w * h) / 1e6).toFixed(2);
    },

    newWidth() {
      return this.mode === "Проценты"
        ? Math.round(this.width * this.widthValue / 100)
        : this.widthValue;
    },

    newHeight() {
      return this.mode === "Проценты"
        ? Math.round(this.height * this.heightValue / 100)
        : this.heightValue;
    },

    interpolationHint() {
      return this.interpolations.find(i => i.value === this.interpolation)?.hint;
    }
  },

  watch: {
    widthValue(val) {
      if (this.keepRatio) {
        this.heightValue = Math.round(val * this.height / this.width);
      }
    }
  },

  methods: {
    close() {
      this.open = false;
    },

    confirm() {
      this.$emit("confirm", {
        width: this.newWidth,
        height: this.newHeight,
        interpolation: this.interpolation
      });
      this.close();
    }
  }
};
</script>
